.items {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding-top: 15px;
  padding-bottom: 15px;
  width: 100%;
}
.items:not(:last-child) {
  border-right: 1px solid #c9c9c9;
}

.label {
  font-size: 14px;
  color: #545454;
  margin-bottom: 5px;
}

.quantity {
  font-weight: 700;
}
